import React from "react";
import { hotelsData } from "../data/data";
import Card from "./Card";
import "../styles.css";
import {
  initialStateCountry,
  initialStatePrice,
  initialStateSize,
  initialStateDateFrom,
  initialStateDateTo
} from "../constants";

//Message Alert for wrong dates order

function dateOrder(dateFrom, dateTo) {
  let dateMessage =
    "The check out date must be inferior than the check in date. Please try it again.";
  if (dateTo !== initialStateDateTo && dateTo < dateFrom) {
    alert(dateMessage);
  }
}

function HotelInfo(props) {
  /*
   *Event Handlers
   */
  //Date
  const eventDateFromGlobal = (e) => {
    props.setDateFromState(e.target.value);
  };
  const eventDateToGlobal = (e) => {
    props.setDateToState(e.target.value);
  };
  //Country
  const onChangeCountryGlobal = (e) => {
    props.setCountryState(e.target.value);
  };

  //Price
  const onChangePriceGlobal = (e) => {
    props.setPriceState(e.target.value);
  };
  //Size
  const onChangeSizeGlobal = (e) => {
    props.setSizeState(e.target.value);
  };

  /*
   *Filters
   */
  const filterBy = () => {
    const checkIn = new Date(props.dateFromState).getTime();
    const checkOut = new Date(props.dateToState).getTime();
    //Country Filter
    const newList = hotelsData
      .filter((element) => {
        if (props.country === initialStateCountry) {
          return element;
        }
        if (props.countryState !== initialStateCountry) {
          return element.country === props.countryState;
        }
        return element;
      })

      //Price Filter
      .filter((element) => {
        if (props.priceState === initialStatePrice) {
          return element;
        }
        if (props.priceState !== initialStatePrice) {
          // console.log(element.price, props.priceState);
          return element.price === Number(props.priceState);
        }
        return element;
      })

      //Size Filter
      .filter((element) => {
        if (props.sizeState === initialStateSize) {
          return element;
        }
        if (props.sizeState === "Small Hotel") {
          // console.log(element.rooms, props.sizeState);
          return element.rooms <= 10;
        }
        if (props.sizeState === "Medium Hotel") {
          // console.log(element.rooms, props.sizeState);
          return element.rooms > 10 && element.rooms <= 20;
        }
        if (props.sizeState === "Large Hotel") {
          // console.log(element.rooms, props.sizeState);
          return element.rooms > 20;
        }
        return element;
      })

      //Date Filter
      .filter((element) => {
        if (props.dateFromState && props.dateToState) {
          if (props.dateFromState <= props.dateToState) {
            const dateHotel =
              checkIn <= element.availabilityTo &&
              checkOut >= element.availabilityFrom;
            return dateHotel;
          }
        }
        return element;
      });
    // console.log(newList);
    return newList;
  };
  let listFilter = filterBy();

  //Message Alert for wrong dates order call
  dateOrder(props.dateFromState, props.dateToState);

  /**
   * Clear All Button
   */
  const resetFilters = () => {
    props.setDateFromState(initialStateDateFrom);
    props.setDateToState(initialStateDateTo);
    props.setCountryState(initialStateCountry);
    props.setPriceState(initialStatePrice);
    props.setSizeState(initialStateSize);
  };

  /**
   *
   */
  return (
    <>
      <main>
        <div className="filters-container">
          <div className="inputsDate">
            <input
              value={props.dateFromState}
              onChange={eventDateFromGlobal}
              type="date"
            />
            <input
              value={props.dateToState}
              onChange={eventDateToGlobal}
              type="date"
            />
          </div>
          <div className="selects">
            <select value={props.countryState} onChange={onChangeCountryGlobal}>
              <option value={initialStateCountry}>{initialStateCountry}</option>
              <option value="Argentina">Argentina</option>
              <option value="Brasil">Brazil</option>
              <option value="Chile">Chile</option>
              <option value="Uruguay">Uruguay</option>
            </select>
            <select value={props.priceState} onChange={onChangePriceGlobal}>
              <option value={initialStatePrice}>{initialStatePrice}</option>
              <option value={1}>Cheapest</option>
              <option value={2}>Cheaper</option>
              <option value={3}>Standard</option>
              <option value={4}>Expensive</option>
            </select>
            <select value={props.sizeState} onChange={onChangeSizeGlobal}>
              <option value={initialStateSize}>{initialStateSize}</option>
              <option value="Small Hotel">Small Hotel</option>
              <option value="Medium Hotel">Medium Hotel</option>
              <option value="Large Hotel">Large Hotel</option>
            </select>
            <button onClick={resetFilters}>Clear All</button>
          </div>
        </div>
        <div className="hotelCards">
          {listFilter.length > 0 ? (
            listFilter.map((hotelCard) => (
              <Card
                key={hotelCard.slug}
                name={hotelCard.name}
                photo={hotelCard.photo}
                description={hotelCard.description}
                rooms={hotelCard.rooms}
                city={hotelCard.city}
                country={hotelCard.country}
                price={hotelCard.price}
              />
            ))
          ) : (
            <div className="Nothing-to-Show">"Nothing to Show :c"</div>
          )}
        </div>
      </main>
    </>
  );
}
export default HotelInfo;
