import React, { useState } from "react";
import Header from "./Components/Header";
import HotelInfo from "./Components/HotelCards";
import {
  initialStateCountry,
  initialStatePrice,
  initialStateSize,
  initialStateDateFrom,
  initialStateDateTo
} from "./constants";

export default function App() {
  //States
  //Dates State
  const [dateFrom, setDateFrom] = useState(initialStateDateFrom);
  const [dateTo, setDateTo] = useState(initialStateDateTo);

  //Country select state
  const [country, setCountry] = useState(initialStateCountry);

  //Price select state
  const [price, setPrice] = useState(initialStatePrice);

  //Size select state
  const [size, setSize] = useState(initialStateSize);

  return (
    <div className="App">
      <Header
        dateFromState={dateFrom}
        dateToState={dateTo}
        countryState={country}
        priceState={price}
        sizeState={size}
      />

      <HotelInfo
        dateFromState={dateFrom}
        setDateFromState={setDateFrom}
        dateToState={dateTo}
        setDateToState={setDateTo}
        countryState={country}
        setCountryState={setCountry}
        priceState={price}
        setPriceState={setPrice}
        sizeState={size}
        setSizeState={setSize}
      />
    </div>
  );
}
