import {} from "@fortawesome/free-solid-svg-icons";
import React from "react";

const months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December"
];

function Header(props) {
  //Conversion of yyyy-mm-ddd to a more understandable date
  const convertDate = (dateString) => {
    const tempDate = dateString.split("-");
    return (
      months[Number(tempDate[1]) - 1] + " " + tempDate[2] + " of " + tempDate[0]
    );
  };
  //conversion of int to string
  const convertPrice = (priceNumber) => {
    priceNumber = Number(priceNumber);
    if (priceNumber === 1) return "Cheapest";
    if (priceNumber === 2) return "Cheaper";
    if (priceNumber === 3) return "Standard";
    if (priceNumber === 4) return "Expensive";
  };
  const convertCountry = (country) => {
    if (country === "Argentina") return "Argentina";
    if (country === "Brasil") return "Brasil";
    if (country === "Chile") return "Chile";
    if (country === "Uruguay") return "Uruguay";
  };
  const convertSize = (size) => {
    if (size === "Small Hotel") return "Small Hotel";
    if (size === "Medium Hotel") return "Medium Hotel";
    if (size === "Large Hotel") return "Large Hotel";
  };

  return (
    <div className="header-container">
      <div>
        <h1>Hotel Reservation</h1>
        <br />
        {
          <h2>
            {props.dateFromState &&
              props.dateToState &&
              `From  ${convertDate(props.dateFromState)} To  ${convertDate(
                props.dateToState
              )} in`}{" "}
            {convertCountry(props.countryState) &&
              `${props.countryState} with a`}{" "}
            {convertPrice(props.priceState) &&
              `${convertPrice(props.priceState)}`}{" "}
            {convertSize(props.sizeState) && `${props.sizeState}.`}
          </h2>
        }
      </div>
    </div>
  );
}
export default Header;
